
// Core variables and mixins
@import 'modules/variables';
@import 'modules/mixins/mixins';

//Components
@import 'modules/button';
@import 'modules/features';
@import 'modules/questions';
@import 'modules/table';
@import 'modules/card';
@import 'modules/custom-card';
@import 'modules/utilities';
@import 'modules/input';
@import 'modules/helper';
@import 'modules/dashboard';
@import 'modules/rangeslider';

// responsive
@import 'modules/responsive';

#sidebar .response-dd {
    position: relative;
    display: block;
    margin: 0;
    padding: 0;
    max-width: 600px;
    list-style: none;
    font-size: 13px;
    line-height: 20px;
}

.response-dd-list {
    display: block;
    position: relative;
    margin: 0;
    padding: 0;
    list-style: none;
}

.response-dd-list .response-dd-list {
    padding-left: 30px;
}

.response-dd-collapsed .response-dd-list {
    display: none;
}

#sidebar .response-dd-item,
.response-dd-empty,
.response-dd-placeholder {
    display: block;
    position: relative;
    margin: 0;
    padding: 0;
    min-height: 20px;
    font-size: 13px;
    line-height: 20px;
}

.response-dd-handle {
    display: block;
    height: 30px;
    margin: 5px 0;
    padding: 5px 10px;
    color: #6F7B8A;
    text-decoration: none;
    font-weight: bold;
    border: 1px solid #e5e9ec;
    background: #fff;
    -webkit-border-radius: 3px;
    border-radius: 3px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
}

.response-dd-handle:hover {
    background-color: #fbfcfd;
}

.response-dd-item>button {
    display: block;
    position: relative;
    cursor: pointer;
    float: left;
    width: 25px;
    height: 20px;
    margin: 5px 0;
    padding: 0;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    border: 0;
    background: transparent;
    font-size: 12px;
    line-height: 1;
    text-align: center;
    font-weight: bold;
}

.response-dd-item>button:before {
    content: '+';
    display: block;
    position: absolute;
    width: 100%;
    text-align: center;
    text-indent: 0;
}

.response-dd-item>button[data-action="collapse"]:before {
    content: '-';
}

.response-dd-placeholder,
.response-dd-empty {
    margin: 5px 0;
    padding: 0;
    min-height: 30px;
    background: #f2fbff;
    border: 1px dashed #b6bcbf;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
}

.response-dd-empty {
    border: 1px dashed #bbb;
    min-height: 100px;
    background-color: #e5e5e5;
    background-image: -webkit-linear-gradient(45deg, #fff 25%, transparent 25%, transparent 75%, #fff 75%, #fff), -webkit-linear-gradient(45deg, #fff 25%, transparent 25%, transparent 75%, #fff 75%, #fff);
    background-image: -moz-linear-gradient(45deg, #fff 25%, transparent 25%, transparent 75%, #fff 75%, #fff), -moz-linear-gradient(45deg, #fff 25%, transparent 25%, transparent 75%, #fff 75%, #fff);
    background-image: linear-gradient(45deg, #fff 25%, transparent 25%, transparent 75%, #fff 75%, #fff), linear-gradient(45deg, #fff 25%, transparent 25%, transparent 75%, #fff 75%, #fff);
    background-size: 60px 60px;
    background-position: 0 0, 30px 30px;
}

.response-dd-dragel {
    position: absolute;
    pointer-events: none;
    z-index: 9999;
}

.response-dd-dragel>.response-dd-item .response-dd-handle {
    margin-top: 0;
}

.response-dd-dragel .response-dd-handle {
    -webkit-box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, .1);
    box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, .1);
}

.nestable-lists {
    display: block;
    clear: both;
    padding: 0;
    width: 100%;
    border: 0;
}

#nestable-menu {
    padding: 0;
    margin: 20px 0;
}

#nestable-output,
#nestable2-output {
    width: 100%;
    height: 7em;
    font-size: 0.75em;
    line-height: 1.333333em;
    font-family: Consolas, monospace;
    padding: 5px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
}

.dark .response-dd-handle {
    color: #6F7B8A;
    border: none;
    background: #d9e0e4;
}

.dark .response-dd-handle:hover {
    background: #d1dade;
    color: #505458;
}

.dark .response-dd-item>button:before {
    color: #8E9AA2;
}

@media only screen and (min-width: 700px) {
    .response-dd {
        float: left;
        width: 48%;
    }
    .response-dd+.response-dd {
        margin-left: 2%;
    }
}

.response-dd-hover>.response-dd-handle {
    background: #2ea8e5 !important;
}

.response-dd3-content {
    display: block;
    height: 30px;
    margin: 5px 0;
    padding: 5px 10px 5px 40px;
    color: #333;
    text-decoration: none;
    font-weight: bold;
    border: 1px solid #ccc;
    background: #fafafa;
    background: -webkit-linear-gradient(top, #fafafa 0%, #eee 100%);
    background: -moz-linear-gradient(top, #fafafa 0%, #eee 100%);
    background: linear-gradient(top, #fafafa 0%, #eee 100%);
    -webkit-border-radius: 3px;
    border-radius: 3px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
}

.response-dd3-content:hover {
    color: #2ea8e5;
    background: #fff;
}

.response-dd-dragel>.dd3-item>.dd3-content {
    margin: 0;
}

.response-dd3-item>button {
    margin-left: 30px;
}

.response-dd3-handle {
    position: absolute;
    margin: 0;
    left: 0;
    top: 0;
    cursor: pointer;
    width: 30px;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    border: 1px solid #aaa;
    background: #ddd;
    background: -webkit-linear-gradient(top, #ddd 0%, #bbb 100%);
    background: -moz-linear-gradient(top, #ddd 0%, #bbb 100%);
    background: linear-gradient(top, #ddd 0%, #bbb 100%);
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.response-dd3-handle:before {
    content: '≡';
    display: block;
    position: absolute;
    left: 0;
    top: 3px;
    width: 100%;
    text-align: center;
    text-indent: 0;
    color: #fff;
    font-size: 20px;
    font-weight: normal;
}

.response-dd3-handle:hover {
    background: #ddd;
}


